//стилизация бэкдропа

.backdrop {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  margin-top: 0;
  background-color: $backdrop-color;
  overflow-y: scroll;

  padding-left: 20px;
  padding-right: 20px;
  padding-top: 20px;
  padding-bottom: 20px;

  @media screen and (min-width: 768px) {
    padding-left: 75px;
    padding-right: 75px;
    padding-bottom: 200px;
    padding-top: 231px;
  }

  @media screen and (min-width: 1024px) {
    padding-top: 50px;
  }
}

// базовая разметка модалки

.modal {
  background-color: $white-color;
  position: absolute;
  transform: translateX(-50%) translateY(-5%);
  top: 93px;
  left: 50%;
  overflow-y: scroll;

  width: 280px;
  height: 958px;

  padding-left: 20px;
  padding-right: 20px;
  padding-top: 48px;
  padding-bottom: 20px;

  @media screen and (min-width: 768px) {
    width: 618px;
    height: 562px;
    transform: translateX(-50%) translateY(25%);

    padding-left: 28px;
    padding-right: 28px;
    padding-bottom: 40px;
    padding-top: 48px;
  }

  @media screen and (min-width: 1024px) {
    width: 882px;
    height: 568px;
    transform: translateX(-50%) translateY(0);

    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 40px;
    padding-top: 50px;
  }
}

//стилизация крестика
.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  margin: 0;

  display: flex;
  padding: 0;
  align-items: center;
  justify-content: center;

  background-color: transparent;
  border: none;

  transition: fill $anim-duration $anim-timing;
}

.close-btn:hover {
  cursor: pointer;

  background-color: lightblue;
  fill: $accent-btn-color;
}

.close-btn__icon {
  box-sizing: border-box;
}

