//обертка

.modal__wrap {
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
    align-items: space-between;
  }
}

//стилизация картинки

.modal__img {
  margin-bottom: 30px;

  @media screen and (min-width: 768px) {
    width: 264px;
    height: 374px;
    margin-right: 30px;
  }

  @media screen and (min-width: 1024px) {
    width: 396px;
    height: 478px;
  }
}

//заголовок

.modal__title {
  margin-bottom: 20px;

  @media screen and (min-width: 768px) {
    margin-top: 0;
  }
}

//стилизация списков

.modal__list {
  display: flex;
  margin-bottom: 20px;

  align-items: center;
  font-size: 12px;
  line-height: 16px;
}

.modal__params {
  margin-right: 40px;
}

.modal__item {
  color: $secondary-text-color;

  &:not(:first-child) {
    margin-top: 10px;
  }
}

.modal__meaning {
  &:not(:first-child) {
    margin-top: 10px;
  }
}

.modal__meaning--orange {
  padding-left: 7px;
  padding-right: 7px;

  border-radius: 20%;
  border: 1px solid $accent-btn-color;
  background-color: $accent-btn-color;
  color: $white-color;
}

.modal__meaning--grey {
  padding-left: 7px;
  padding-right: 7px;

  border-radius: 20%;
  border: 1px solid $beige-color;
  background-color: $beige-color;
}

//описание

.modal__desc {
  margin-bottom: 20px;
  margin-top: 10px;

  font-size: 12px;
  line-height: 20px;
}

//кнопки

.modal__wrap--btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;

  @media screen and (min-width: 1024px) {
    justify-content: flex-start;
  }
}

//скрытие модалки

.modal--close {
  display: none;
}
